/* App.css */
@import url('https://fonts.googleapis.com/css?family=Exo:400,700');

:root{
    --bgcolor: #c7574d;
    --textcolor:white;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Exo', sans-serif;
    overflow: hidden;
}

.area {
    background: var(--bgcolor);
    width: 100%;
    height: 100vh;
}

.glass-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 90vh;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--textcolor);
    opacity: 0;
    animation: fadeIn 1s forwards;
}

.content {
    max-width: 900px;
    margin: 0 auto;
    font-size: 1rem;
    line-height: 1.6;
    animation: fadeIn 1s forwards;
}

.btn {
    background-color: var(--textcolor);
    color: var(--bgcolor);
}



@media (min-width: 1200px) {
    .image-container img {
        width: 500px;
        max-height: 500px;
        height: auto;
    }

    .button-container {
        display: flex;
        justify-content: center;
        gap: 1rem;
        margin-top: 1rem;
        width: 100%;
    }
    .button-container.two-buttons {
        justify-content: center;
    }

    .button-container.three-buttons {
        grid-template-columns: repeat(3, 1fr);
    }
    .content{
        font-size: 1.6rem;
    }
    .btn {
        padding: 0rem 2rem;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        font-size: 1.3rem;
        transition: all 0.3s ease;
        min-width: 380px;
        height: 80px;
    }

    .columns-container {
        display: flex;
        justify-content: space-around;
        
        gap: 20px;
        margin-top: 1rem;
      }
      
      .column {
        flex: 1; /* 讓每一欄平分寬度 */
        padding: 20px;
        box-sizing: border-box;
        font-size:1.2rem;
        text-align: justify;
        background-color: rgba(255, 255, 255, 0.2);
        
        
      }
      .row {
        display: flex;
        justify-content: center; /* 方塊置中 */
        gap: 16px; /* 方塊之間的間距 */
        margin-bottom: 1px; /* 每排之間的間距 */
        
      }
      
      
      
}

@media (max-width: 1200px) {
    .image-container img {
        width: 250px;
        max-height: 306px;
        height: auto;
    }

    .button-container {
        display: flex;
        justify-content: center;
        gap: 1rem;
        margin-top: 1rem;
        width: 100%;
    }
    
    .button-container.three-buttons {
        flex-direction: column;
        align-items: center;
        
    }
    .btn {
        padding: 0rem 1rem;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        font-size: 1rem;
        transition: all 0.3s ease;
        width: 300px;
        height: 70px;
    }
}



.btn:hover {
    transform: translateY(-5px);
    background-color: var(--bgcolor);
    color: var(--textcolor)
}

.typing-text {
    min-height: 1em;
    margin-bottom: 1rem;
    white-space: pre-wrap;
}

.image-container {
    display: flex;
    justify-content: center;
    margin: 1rem 0;
}



@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}




.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    pointer-events: none;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}
